    <!-- FIN: Modal EDIT Tarea -->

  <!-- GESTION OPERARIOS -->
    <div class="wn-col col-asignadas">
      <div class="wn-col-title ">Operarios
        <el-button v-if="!searchVisibleOperario" @click="searchVisibleOperario = true"  class="wn-menu-btn" icon="el-icon-search">Abrir</el-button>
        <el-button v-if="searchVisibleOperario"  @click="searchVisibleOperario = false; searchWordOperario = ''" class="wn-menu-btn" icon="el-icon-search">Cerrar</el-button>
        <el-button @click="inputVisible=false,cleanFormOperarios(),operariosVisible = true" class="wn-menu-btn">Crear nuevo</el-button>
      </div>

      <div class="table-width">
        <transition name="bounce">
          <b-input-group v-if="searchVisibleOperario" class="fondo-buscador">
