        </el-form-item>
        <el-form-item label="Repetir contrasea:"  required >
          <el-input  type="password" v-model="frmO_pass_2" placeholder="123456"></el-input>
        </el-form-item>


        <el-form-item label="Etiquetas:"  required>
          <el-tag
            :key="tag"
            v-for="tag in frmO_etiquetas"
            closable
            :disable-transitions="false"
            @close="handleCloseO(tag)">
            {{tag}}
          </el-tag>

          <el-autocomplete
            style="width: 23%; font-size: 10px"
