          <el-button type="primary" @click="createWorker">Crear</el-button>
        </span>
    </el-dialog>
    <!-- FIN: Modal NUEVO Operario-->

    <!-- Modal EDITAR Operario-->
    <el-dialog
      title="Editar operario"
      :visible.sync="operariosEditVisible"
      width="65%">

      <el-form ref="modal_edit_worker" label-position="left" label-width="120px">
        <el-form-item label="Nombre:"  required >
          <el-input  type="text" v-model="frmO_nombre" placeholder="Nombre del operario"></el-input>
        </el-form-item>
        <el-form-item label="Apellidos:"  required >
          <el-input  type="text" v-model="frmO_apellidos" placeholder="Apellidos del operario"></el-input>
        </el-form-item>
        <el-form-item label="Contrasea:"  required >
          <el-input  type="password" v-model="frmO_pass" placeholder="123456"></el-input>
        </el-form-item>
        <el-form-item label="Etiquetas:"  required>
          <el-tag
            :key="tag"
            v-for="tag in frmO_etiquetas"
            closable
            :disable-transitions="false"
            @close="handleCloseO(tag)">
            {{tag}}
          </el-tag>

          <el-autocomplete
            style="width: 23%; font-size: 10px"
            class="input-new-tag"
            v-if="inputVisibleOp"
            v-model="inputValueOp"
            ref="saveTagInputOp"
            :fetch-suggestions="querySearch"
            placeholder="Nombre nueva etiqueta"
            @keyup.enter.native="handleInputConfirmOp"
            @select="handleSelectTagOp">
          </el-autocomplete>

          <el-button v-else class="button-new-tag" size="small" @click="showInputOp">+ Nuevo tag</el-button>
        </el-form-item>

      </el-form>

      <span slot="footer" class="dialog-footer">
