          <el-button type="primary" @click="editWorker">Guardar</el-button>
        </span>
    </el-dialog>
    <!-- FIN: Modal EDITAR Operario-->


    <!-- TAREAS REALIZADAS -->
    <div class="wn-col col-realizadas">
      <div class="wn-col-title ">Histrico de tareas
        <el-button v-if="!searchVisibleFinalizadas" @click="searchVisibleFinalizadas = true"  class="wn-menu-btn" icon="el-icon-search">Abrir</el-button>
        <el-button v-if="searchVisibleFinalizadas"  @click="searchVisibleFinalizadas = false; searchWordFinalizadas = ''" class="wn-menu-btn" icon="el-icon-search">Cerrar</el-button>
      </div>


      <div class="table-width">
        <transition name="bounce">
          <b-input-group v-if="searchVisibleFinalizadas" class="fondo-buscador">
            <b-form inline>
              <i class="material-icons prefix">search</i>
              <b-input style="width: 300px; margin-left: 10px; margin-right: 8px" v-model="searchWordFinalizadas" placeholder="Buscar" />
              <i :disabled="!searchWordFinalizadas" @click="searchWordFinalizadas = ''" class="fa fa-times-circle prefix"></i>
            </b-form>
          </b-input-group>
        </transition>
        <div class="wn-col-container" :class="[searchVisibleFinalizadas ? 'searchOn' : '']">
          <div v-if="filteredFinalizadas.length<=0">
            <p class="informacion">No se encontraron tareas finalizadas</p>
          </div>

          <div v-if="filteredFinalizadas.length>0" v-for="task in filteredFinalizadas" v-bind:key="task.id" class="wn-task-container">
            <Task
              :id=task.id
              :titulo=task.titulo
              :operario=task.operario
              :duracion=task.duracion
              :estimado=task.estimado
              :prioridad=task.prioridad
              :showMore=task.showMore
              :descripcion=task.descripcion
              :pausable=task.pausable
              :tags=task.tags
              :h_inicio="task.h_inicio"
            />
            <div class="wn-btn-div">
              <button v-if="!task.showMore" @click="task.showMore=true" class="wn-menu-btn"><i class="fa fa-eye " aria-hidden="true"></i></button>
              <button v-if="task.showMore" @click="task.showMore=false" class="wn-menu-btn"><i class="fa fa-eye-slash " aria-hidden="true"></i></button>
            </div>
          </div>
        </div>
    </div>

    </div>

  </div>
</template>

<script>
  import Navbar from './Navbar';
  import Task from './Task';
  import Worker from './Worker';
  import db from './firebaseInit';
  import ButtonGroup from "bootstrap-vue/es/components/button-group/button-group";
  import InputTag from 'vue-input-tag';

  export default {
    name: 'gTemplates',
    data() {
      return {

        operariosVisible: false,
        nuevaPlantillaVisible: false,

        searchVisible: false,
        searchVisibleOperario:    false,
        searchVisibleFinalizadas: false,

        dialogEditVisible: false,
        operariosEditVisible: false,

        templates: [],
        operarios: [],
        tasks_realizadas: [],

        // Modelo de datos del form Plantillas
        frmP_operario: '',
        frmP_asignable:   true,
        frmP_titulo:        '',
        frmP_descripcion:   '',
        frmP_estimado:      '',
        frmP_prioridad:    200,
        frmP_pausable:   false,
        frmP_etiquetas:     [],

        // Modelo de datos del form Operario
        frmO_nombre: '',
        frmO_apellidos: '',
        frmO_pass: '',
        frmO_pass_2: '',
        frmO_conectado: false,
        frmO_tareas:    [],
        frmO_etiquetas: [],

        prioridades: [
          { value: 400, text: 'Urgente' },
          { value: 300, text: 'Alta' },
          { value: 200, text: 'Media' },
          { value: 100, text: 'Baja' },
          { value: 0,   text: 'Muy baja' },
        ],

        searchWord: '',
        searchWordOperario: '',
        searchWordFinalizadas: '',

        tagRecomendations: [],
        inputVisible: false,
        inputValue: '',
        inputVisibleOp: false,
        inputValueOp: '',
        id:'',
        loading: true
      }
    },
    created: function () {
      db
        .collection('plantillas')
        .get()
        .then(querySnapshot => {
          this.loading = false;
          querySnapshot.forEach(doc => {
            const template = {
              id: doc.id,
              operario: 'Se asignar durante la ejecucin',
              titulo: doc.data().titulo,
              duracion: 0,
              estimado: doc.data().estimado,
              prioridad: doc.data().prioridad,
              descripcion: doc.data().descripcion,
              showMore: false,
              pausable: doc.data().pausable,
              tags: doc.data().etiquetas,
            };
            this.templates.push(template);

          });
        });

      db
        .collection('operarios')
        .onSnapshot(querySnapshot => {
          this.loading = false;
          this.operarios.length=0
          querySnapshot.forEach(doc => {
            const operario = {
              nombre: doc.data().nombre,
              apellidos: doc.data().apellidos,
              pass: doc.data().pass,
              id: doc.id,
              tags: doc.data().etiquetas,
              showMore: false,
              conectado: doc.data().conectado
            }

            this.operarios.push(operario)

            this.operarios.sort(function(a, b) {
              return !a.conectado && b.conectado
            });
          });
        });

      db.collection('finalizadas')
      //this.tasks_realizadas.length=0
        .onSnapshot(querySnapshot => {
          this.tasks_realizadas.length=0;
          querySnapshot.forEach(doc => {
            const task = {
              id: doc.id,
              operario: doc.data().operario,
              titulo: doc.data().titulo,
              duracion: Math.round((doc.data().h_fin.getTime()-doc.data().h_inicio.getTime())/60000),
              estimado: doc.data().estimado,
              prioridad: doc.data().prioridad,
              showMore: false,
              descripcion: doc.data().descripcion,
              pausable: doc.data().pausable,
              tags: doc.data().etiquetas,
              h_inicio: doc.data().h_inicio
            };
            this.tasks_realizadas.push(task);
            this.tasks_realizadas.sort(function(a, b) {
              return b["prioridad"] - a["prioridad"] || a["id"] - b["id"];
            });
          });
        });
    }
     ,
    computed: {
      filteredTemplates: function(){
        return this.templates.filter((template) => {
          return template.titulo.toLowerCase().match(this.searchWord.toLowerCase())
        })
      },
      filteredOperarios: function(){
        return this.operarios.filter((operario) => {
          return operario.apellidos.toLowerCase().match(this.searchWordOperario.toLowerCase())
        })
      },

        filteredFinalizadas: function(){
        return this.tasks_realizadas.filter((task) => {
          return task.titulo.toLowerCase().match(this.searchWordFinalizadas.toLowerCase())
        })
      }
    },
    methods: {

      querySearch(queryString, cb) {

        var tagRecomendations = this.tagRecomendations;
        var results = queryString ? tagRecomendations.filter(this.createFilter(queryString)) : tagRecomendations;
        // call callback function to return suggestions
        cb(results);
      },
      createFilter(queryString) {
        return (coso) => {
          return (coso.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelectTag(item) {
        this.handleInputConfirm();
      },
      handleSelectTagOp(item) {
        this.handleInputConfirmOp();
      },

      loadTags() {
        var tags = [];

        db.collection('etiquetas').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {

            const t = {
              "value": doc.data().tag
            };
            tags.push(t);
          });
        });
        return tags;
      },

      createTemplate (evt) {
        evt.preventDefault()
        if (!this.frmP_titulo) {
          alert('El ttulo de la plantilla no puede estar vaco')
        }
        else if (!this.frmP_estimado) {
          alert('El tiempo estimado no puede estar vaco')
        }
        else if (isNaN(this.frmP_estimado)) {
          alert('El tiempo estimado debe ser un nmero entero')
        }
        else {
          this.nuevaPlantillaVisible = false;
          this.persistData()
        }
      },

      editTemplate (evt) {

        evt.preventDefault()
        if (!this.frmP_titulo) {
          alert('El ttulo de la plantilla no puede estar vaco')
        }
        else if (!this.frmP_estimado) {
          alert('El tiempo estimado no puede estar vaco')
        }
        else if (isNaN(this.frmP_estimado)) {
          alert('El tiempo estimado debe ser un nmero entero')
        }
        else {
          this.dialogEditVisible = false;
          this.updateTemplate(this.id)
        }
      },
      fillData(id){

        var opRef = db.collection("plantillas").doc(id.toString());

        opRef.get()
          .then(doc => {
            this.frmP_operario = doc.data().operario,
              this.frmP_asignable = doc.data().asignable,
              this.frmP_titulo = doc.data().titulo,
              this.frmP_descripcion = doc.data().descripcion,
              this.frmP_estimado=  doc.data().estimado,
              this.frmP_prioridad=  doc.data().prioridad,
              this.frmP_pausable = doc.data().pausable,
              this.frmP_etiquetas = doc.data().etiquetas,
              this.id = doc.id
          }).catch(function(error) {
          console.log("Error gettings document:", error);
        });
      },

      fillDataOp(id){
        var opRef = db.collection("operarios").doc(id.toString());

        opRef.get()
          .then(doc => {
            this.frmO_nombre    = doc.data().nombre,
            this.frmO_apellidos = doc.data().apellidos,
            this.id             = doc.data().id;
            this.frmO_etiquetas = doc.data().etiquetas
          }).catch(function(error) {
          console.log("Error gettings document:", error);
        });
      },

      updateTemplate(id) {

        var tsRef = db.collection("plantillas").doc(id.toString());

        return tsRef.update({
          descripcion: this.frmP_descripcion,
          estimado: parseInt(this.frmP_estimado),
          etiquetas: this.frmP_etiquetas,
          pausable: this.frmP_pausable,
          prioridad: this.frmP_prioridad,
          titulo: this.frmP_titulo,

        }).then(function() {
          console.log("Plantilla actualizada con xito!");
        }).then(() => {
          this.handleEdit(id)
        }).catch(function(error) {
          console.error("Error actualizando la plantilla: ", error);
        });
      },
      persistData () {

        var autRef = db.collection('autoIncrems').doc('plantillas');
        var transaction = db.runTransaction( async t => {
          return t.get(autRef)
            .then(doc => {

              var newTemplate = doc.data().plantillas + 1;
              this.id = newTemplate;
              db.collection('plantillas').doc(newTemplate.toString()).set({

                asignable: true,
                descripcion: this.frmP_descripcion,
                estimado: parseInt(this.frmP_estimado),
                etiquetas: this.frmP_etiquetas,
                pausable: this.frmP_pausable,
                prioridad: this.frmP_prioridad,
                titulo: this.frmP_titulo,
                id: newTemplate

              }).then(docRef => {
                console.log('Plantilla aadida a FireBase!')

                for (var i = 0; i < this.frmP_etiquetas.length; i++) {
                  var eti = this.frmP_etiquetas[i].toString();

                  this.persistTag(eti);
                }
              }).then(() => {
                this.handleSubmit()

              }).catch(error => {
                console.error('Error aadiendo la plantilla!',error)
              });
              t.update(autRef, {plantillas: newTemplate});

            })
        })

      },

      deleteTemplate(id) {

        if (confirm('Estas seguro que quieres borrar la plantilla con ID: ' + id + ' ?')) {
          var evalTags = [];
          var ref = db.collection('plantillas').doc(id.toString());

          return ref.get()
            .then(doc => {
              evalTags = doc.data().etiquetas;
              for (var i = 0; i < evalTags.length; i++) {
                this.decreaseTag(evalTags[i])
              }
            }).then(doc => {
              db.collection('plantillas').doc(id.toString()).delete().then(function () {
                console.log("Plantilla borrada con xito!");
              }).catch(function (error) {
                console.error("Error borrando plantilla!: ", error);
              });

              this.removeFromModel(id);
            })
        }
      },

      removeFromModel(id){

        var idx = -1;
        var i = 0;
        this.templates.forEach(function(task) {
          if(task.id == id){
            idx=i;
          };
          i++;
        });
        this.templates.splice(idx,1);
      },

      removeFromModelOp(id){
        var idx = -1;
        var i = 0;
        this.operarios.forEach(function(operario) {
          if(operario.id == id){
            idx=i;
          };
          i++;
        });
        this.operarios.splice(idx,1);
      },


      decreaseTag(tag){

        var ref = db.collection('etiquetas').doc(tag);

        return ref.get()
          .then(doc => {
            var num = doc.data().veces;
            console.log(num)
            if(num > 1){
              ref.update({veces:num-1})
            }else{
              ref.delete().then(function() {
                console.log("Etiqueta borrada con xito!");
              }).catch(function(error) {
                console.error("Error borrando etiqueta!: ", error);
              });
            }
          })

      },
      persistTag(tag){

        var refTag = db.collection('etiquetas').doc(tag);

        var getDoc = refTag.get().then(doc => {

          if (!doc.exists) {
            refTag.set({tag: tag, veces: 1})
              .then(docRef => {
                console.log('Nueva etiqueta aadida a FireBase!: '+tag)
                this.tagRecomendations = this.loadTags();
              })
              .catch(error => {console.error('Error aadiendo la etiqueta!: '+tag, error)});
          } else {
            var updateVeces = refTag.set({tag: tag, veces: doc.data().veces + 1})
              .then(docRef => {console.log('Etiqueta incrementada en FireBase!: '+tag)})
          }
        })
          .catch(err => {console.log('Error getting document', err);});
      },
      handleEdit(id) {

        var i = 0;
        for(i=0; i<this.templates.length; i++){

          if(this.templates[i].id == id){
            this.templates[i].titulo=this.frmP_titulo;
            this.templates[i].operario=this.frmP_operario;
            this.templates[i].pausable=this.frmP_pausable;
            this.templates[i].prioridad=this.frmP_prioridad;
            this.templates[i].estimado=this.frmP_estimado;
            this.templates[i].descripcion=this.frmP_descripcion;
            this.templates[i].etiquetas=this.frmP_etiquetas;
          }
        }

        this.dialogEditVisible = false;
        this.cleanForm();
      },
      handleSubmit () {

        this.templates.push(
          {
            titulo: this.frmP_titulo,
            operario: "",
            duracion: 0,
            estimado:parseInt(this.frmP_estimado),
            id: this.id
          }

        )
        this.nuevaPlantillaVisible = false;
        this.cleanForm();
      },

      handleClose(tag) {
        this.frmP_etiquetas.splice(this.frmP_etiquetas.indexOf(tag), 1);
      },

      handleCloseO(tag) {
        this.frmO_etiquetas.splice(this.frmO_etiquetas.indexOf(tag), 1);
      },

      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },

      showInputOp() {
        this.inputVisibleOp = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputOp.$refs.input.focus();
        });
      },

      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {

          this.frmP_etiquetas.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },

      handleInputConfirmOp() {
        let inputValueOp = this.inputValueOp;
        if (inputValueOp) {

          this.frmO_etiquetas.push(inputValueOp);
        }
        this.inputVisibleOp = false;
        this.inputValueOp = '';
      },


      cleanForm () {

        this.frmP_titulo='';
        this.frmP_descripcion='';
        this.frmP_estimado='';
        this.frmP_prioridad=200;
        this.frmP_pausable=false;
        this.frmP_etiquetas=[]
        this.id = '';
      },

      cleanFormOperarios() {
        this.frmO_nombre = '';
        this.frmO_apellidos = '';
        this.frmO_pass   = '';
        this.frmO_pass_2 = '';
        this.id='';
        this.frmO_etiquetas=[];
      },


      /* METODOS OPERARIOS */
      createWorker(evt) {
        evt.preventDefault()
        if (!this.frmO_nombre) {
          alert('El nombre no puede estar vaco')
        }
        else if (!this.frmO_apellidos) {
          alert('Los apellidos no puden estar vacios')
        }
        else if (!this.frmO_pass) {
          alert('Se requiere un Password')
        }
        else if (!this.frmO_pass_2) {
          alert('Se requiere confirmacin de Password')
        }
        else if (this.frmO_pass != this.frmO_pass_2) {
          alert('Las contraseas no coinciden')
        }
        else if (isNaN(this.frmO_pass)) {
          alert('El Password ha de ser numrico')
        }
        else {
          this.operariosVisible = false;
          this.persistDataOperario()
        }
      },

      editWorker(evt){
        evt.preventDefault()

        if (!this.frm_nombre) {
          alert('El nombre no puede estar vaco')
        }
        else if (!this.frm_apellidos) {
          alert('Los apellidos no pueden estar vacios')
        }
        else if (!this.frm_pass) {
          alert('Se requiere un Password')
        }
        else if (isNaN(this.frm_pass)) {
          alert('El Password ha de ser numrico')
        }
        else {
          this.dialogEditVisible = false;
          this.updateEmployee(this.id)
        }
      },

      persistDataOperario() {
        var autRef = db.collection('autoIncrems').doc('operarios');
        var transaction = db.runTransaction( async t => {
          return t.get(autRef)
            .then(doc => {

              var newOp = doc.data().operarios + 1;
              this.id = newOp;
              db.collection('operarios').doc(newOp.toString()).set({

                nombre: this.frmO_nombre,
                apellidos: this.frmO_apellidos,
                etiquetas: this.frmO_etiquetas,
                pass: this.frmO_pass,
                id: newOp,
                conectado: false,
                tareas: []

              }).then(docRef => {
                console.log('Operario aadido a FireBase!')

                for (var i = 0; i < this.frmO_etiquetas.length; i++) {
                  var eti = this.frmO_etiquetas[i].toString();

                  this.persistTag(eti);
                }
              }).then(() => {
                this.cleanFormOperarios()

              }).catch(error => {
                console.error('Error aadiendo el operario!', error)
              });
              t.update(autRef, {operarios: newOp});
            })
        })
      },

      deleteWorker(id) {
        if (confirm('Estas seguro que quieres borrar el operario con ID: ' + id + ' ?')) {
          var evalTags = [];
          var ref = db.collection('operarios').doc(id.toString());

          return ref.get()
            .then(doc => {
              evalTags = doc.data().etiquetas;
              for (var i = 0; i < evalTags.length; i++) {
                this.decreaseTag(evalTags[i])
              }
            }).then(doc => {
              db.collection('operarios').doc(id.toString()).delete().then(function () {
                console.log("Operario borrado con xito!");
              }).catch(function (error) {
                console.error("Error borrando operario!: ", error);
              });
              this.removeFromModelOp(id);
            })
        }
      },




    },

    mounted() {
      this.tagRecomendations = this.loadTags();
    },

    components: {
      ButtonGroup,
      Navbar,
      Task,
      Worker,
      'bootstrap-modal': require('vue2-bootstrap-modal'),
      InputTag
    }
  }
</script>
<style>

  div.wn-col div {
    padding-left:  10px;
    padding-top:    3px;
    padding-bottom: 3px;
  }
  div.wn-col {
    width: 33.3333%;
    float: left;
    margin-top: -9px;
    border: solid 2px white;
    height: 100%;
  }
  div.wn-col-title {
    background: cadetblue;
    color: white;
    font-size: 16px;
    padding-bottom: 2px;
  }

  div.wn-col-container {
    overflow-y:scroll;
    overflow-x: hidden;
    align-content: center;
    background: whitesmoke;
    padding: 0;
    margin:  0;
    height: 600px !important;
  }

  div.wn-col-container::-webkit-scrollbar {
    width: 8px;
    max-width: 8px;
  }

  div.wn-col-container::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
  }

  div.wn-col-container::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
  }

  /*div.searchOff {
    height: 600px !important;
  }*/

  div.searchOn {
    height: 534px !important;
  }

  div.wn-task-container {
    margin-top: 3px;

    height: 80px;
  }
  div.col-pendientes {
    border-left: none;
  }
  div.col-asignadas {
    border-left: none;
    border-right: none;
  }
  div.col-realizadas {
    border-right: none;
  }


  div.wn-task-data {
    width: 80%;
    float: left;
  }

  div.wn-task-data h3 {
    font-size:     16px;
  }

  div.wn-task-data p {
    font-size:     12px;
    padding-top:    4px;
    padding-bottom: 0px;
    margin-bottom:  -6px;
  }

  div.wn-btn-div {
    width: 2px;
    float: right;
  }

  .el-button {
    margin-top: 3px !important;
  }

  button.wn-menu-btn {
    border: none;
    background: whitesmoke;
    color: cadetblue;
    font-size: 12px;
    float: right;
    width: 140px;
    padding-top:    2px;
    padding-bottom: 2px;
    padding-left:  34px;
    padding-right: 34px;
    margin-top:     1px;
    margin-right:  24px;
  }

  div.wn-btn-div .wn-menu-btn {
    font-size: 12px;
    text-align: left;
    width: 20px;
    float: right;
    margin:    0;
  }

  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }

  .table-width {
    height: 600px;
    padding-top:  0  !important;
    padding-left: 0  !important;
    background-color: whitesmoke;
  }

  .fondo-buscador {
    background-color: #ffffff;
    color: #5f9ea0;

    border-left:   solid   2px whitesmoke;
    border-right:  solid   2px whitesmoke;
    border-bottom: solid   1px whitesmoke;
    border-radius: 2px;

    /*border-top:    solid  2px whitesmoke;*/
    /*border-left:   solid   1px #5F9EA0;
    border-right:  solid   1px #5F9EA0;
    border-bottom: solid   1px #5F9EA0;
    border-bottom-radius: 50px;*/
    padding-left: 40px !important;
  }

  .bounce-enter-active {
    animation: bounce-in .1s;
  }
  .bounce-leave-active {
    animation: bounce-in .1s reverse;
  }
  @keyframes bounce-in {
    0% {
      opacity: 0;
      height: 20px;
    }
    50% {
      opacity: 50;
      height: 40px;
    }
    100% {
      opacity: 100;
      height: 73px;
    }
  }

  .wn-menu-btn:focus {
    outline: none;
  }

  /**/
  p.informacion {
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    color: cadetblue;

    padding-top: 66px;
  }

</style>



